import React from 'react';
import img from "../../../asset/img/img";


const product = () => {
    return (
        <>
            {img.map((imagenes,index) => {
                return(
                    <div key={index} className="product__column">
                        <div className="img-container">
                            <div className="img">
                                <img src={imagenes} alt="" />
                            </div>
                        </div>
                        <div className="info">
                            <h2>samsung mobile</h2>
                            <span>100$</span>
                        </div>
                    </div>
                );
            })
            }
        </>
    );
}

export default product;
