import React from 'react';
import img from "../../asset/img/img";
import imgPublish from "../../asset/img/9.png";
import Product from "./product/product";
import "./main.scss";
const main = () => {
    return (
        <>
            <main>
                <div className="container">
                    <section className=" banner flex">
                            <div className="info">
                                <div className="info__container">
                                    <h1 className="text">Get the best product at your home</h1>
                                    <div className="search">
                                        <input type="text" placeholder="search your favorite product" />
                                        <i className="fas fa-search"></i>
                                    </div>
                                </div>
                            </div>
                            <div className="img">
                                <div className="img__container">
                                    <img src={img[0]} alt="" />
                                </div>
                            </div>
                        
                    </section>
                    <section className="category flex">
                        <div className="select">
                            <select name="product" id="" >
                                <option value="See all">See all</option>
                                <option value="Electronics">Electronics</option>
                                <option value="Cosmetics">Cosmetics</option>
                                <option value="Cloths">Cloths</option>
                                <option value="Books">Books</option>
                                <option value="Furniture">Furniture</option>
                            </select>
                        </div>
                        <div className="category-nav">
                            <a href="" className="category-nav__link">Electronics</a>
                            <a href="" className="category-nav__link">Cosmetics</a>
                            <a href="" className="category-nav__link">Cloths</a>
                            <a href="" className="category-nav__link">Books</a>
                            <a href="" className="category-nav__link">Furniture</a>
                            <a href="" className="category-nav__link">See all</a>
                        </div>
                        <div className="category-arrow">
                            <div className="category-arrow__container">
                                <i className="fas fa-arrow-left"></i>
                                <i className="fas fa-arrow-right"></i>
                            </div>
                        </div>
                    </section>
                    <section className="product">
                        <Product />
                    </section>
                    <section className="publish flex">
                        <div className="info">
                            <div className="info-container">
                                <h2>Latest stylish shoe</h2>
                                <p>Men 2020 collection shoe</p>
                                <button className="btn">See all</button>
                            </div>
                        </div>
                        <div className="img">
                            <div className="img-container">
                               <img src={imgPublish} alt="" />
                            </div>
                        </div>
                    </section>
                </div>
            </main>
        </>
    );
}

export default  main;
